html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: Pragmatica;
  font-size: clamp(12px, 1.15vw, 22px);
  font-style: normal;
  font-weight: 400;
  line-height: 130%; /* 20.8px */
  background: #fbfbf6;
}
body.modal-open {
  overflow: hidden;
  max-height: 100vh;
}

.body-container {
  max-width: 1680px;
  margin: 0 auto;
  width: 100vw;
  overflow-x: hidden;
}

.container {
  max-width: 2000px;
  box-sizing: border-box;
}

.content {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

h1,
h2,
h3 {
  color: #2A2835;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
  margin: 0;
  padding: 0;
}

p,
a,
span,
label,
input,
textarea,
tr,
td,
th,
ul {
  font-family: Pragmatica, sans-serif;
  color: #2A2835;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
  text-decoration: none;
  margin: 0;
  padding: 0;
  border: none;
}

input,
textarea {
  background: #fbfbf6 !important;
  border-radius: 0 !important;
}

a:visited {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  background: none;
  border: none;
  cursor: pointer;
  margin: 0;
  padding: 0;
}

input:focus,
textarea:focus {
  outline: 1px solid #1B1A19;
}

.title {
  font-size: clamp(18px, 2.2vw, 34px);
  color: #2a2835;
  font-family: Steinbeck;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.title-1 {
  /* Title 1 */
  font-family: Pragmatica;
  font-size: clamp(16px, 1.6vw, 26px);
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.input-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-checkbox {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 72px;
}
@media (max-width: 430px) {
  .form-checkbox {
    margin-top: 20px;
  }
}

.search-container {
  position: relative;
}

.input {
  display: flex;
  padding: 16px 24px;
  align-items: flex-start;
  gap: 48px;
  font-size: 16px;
  border-radius: 0 !important;
  align-self: stretch;
  border: 1px solid #D7D7D7;
  background: #FBFBF6;
  transition: border 0.9s;
}
@media (max-width: 620px) {
  .input {
    padding: 10px !important;
  }
}

.input-error {
  border: 1px solid #DB1C4A !important;
  background: #FBFBF6 !important;
  color: #DB1C4A !important;
  outline: none !important;
  transition: border 0.9s;
}

.input-label {
  cursor: pointer;
  font-size: clamp(13px, 1.1vw, 18px);
}

.input-readonly {
  display: flex;
  padding: 16px 24px;
  align-items: flex-start;
  gap: 48px;
  font-size: 16px;
  align-self: stretch;
  border: 1px solid #D7D7D7;
  background: #FBFBF6;
  outline: none !important;
  transition: border 0.9s;
}

.textarea-input {
  display: flex;
  height: 130px;
  padding: 16px 0 16px 24px;
  align-items: flex-start;
  gap: 48px;
  font-size: 16px;
  align-self: stretch;
  border: 1px solid #D7D7D7;
  background: #FBFBF6;
  transition: border 0.9s;
}

.textarea-input-error {
  border: 1px solid #DB1C4A !important;
  background: #FBFBF6 !important;
  color: #DB1C4A !important;
  outline: none !important;
  transition: border 0.9s;
}

.select-input {
  display: flex;
  padding: 16px 24px;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  align-self: stretch;
  border: 1px solid #D7D7D7;
  background: url("./../../public/assets/img/icons/arrow-down.svg") no-repeat calc(100% - 24px) center;
  appearance: none;
  outline: none;
  transition: border 0.9s;
}

.select-input:focus {
  background: url("./../../public/assets/img/icons/arrow-left2.svg") no-repeat calc(100% - 24px) center;
}

.select-input-error {
  border: 1px solid #DB1C4A !important;
  background: #FBFBF6 !important;
  color: #DB1C4A !important;
  outline: none !important;
  transition: border 0.9s;
}

.input,
.input-phone {
  display: flex;
  padding: 16px 24px;
  align-items: flex-start;
  gap: 48px;
  font-size: 16px;
  align-self: stretch;
  border: 1px solid #D7D7D7;
  background: #FBFBF6;
  transition: border 0.9s;
}
@media (max-width: 620px) {
  .input,
  .input-phone {
    min-width: 100%;
    max-width: 100%;
    box-sizing: border-box;
  }
}

@media (max-width: 620px) {
  input[type=date] {
    height: 54.5px;
    min-width: 95%;
    align-items: center;
    justify-content: flex-start;
  }
}

.input-phone:focus {
  outline: none;
}

@keyframes checkboxChange {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
input[type=checkbox] {
  display: none;
}

.checkbox-input-label::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  cursor: pointer;
  border: 1px solid #D7D7D7;
  background: #FBFBF6;
  transition: border 0.9s;
}

input[type=checkbox]:checked + .checkbox-input-label::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: url("./../../public/assets/img/icons/checkbox-active.svg") no-repeat center;
  animation: checkboxChange 0.5s ease-in-out;
}

.checkbox-input-label span {
  font-size: clamp(12px, 1.2vw, 16px);
  margin-left: 11px;
  cursor: pointer;
}

@keyframes radioChange {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
input[type=radio] {
  display: none;
}

.radio-input-label::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: url("./../../public/assets/img/icons/radio.svg") no-repeat center;
}

input[type=radio]:checked + .radio-input-label::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: url("./../../public/assets/img/icons/radio-active.svg") no-repeat center;
  animation: radioChange 0.5s ease-in-out;
}

.radio-input-label span {
  font-size: 16px;
  margin-left: 11px;
  cursor: pointer;
}

.search-input, .search-input-black {
  display: flex;
  padding: 20px 8px 20px 0;
  flex-direction: column;
  align-items: flex-start;
  align-self: stretch;
  font-size: 28px;
  color: #8C8C8C;
  width: 100%;
  border-bottom: 1px solid #1B1A19;
  border-radius: 0 !important;
}
@media (max-width: 1000px) {
  .search-input, .search-input-black {
    font-size: 16px;
  }
}
.search-input:focus, .search-input-black:focus {
  outline: none;
}
.search-input::placeholder, .search-input-black::placeholder {
  font-family: Steinbeck;
}
.search-input-black {
  color: #FBFBF6;
  background-color: #1B1A19;
  font-size: 16px;
  padding: 20px;
  box-sizing: border-box;
}

.form {
  display: flex;
  flex-direction: column;
}
.form--gap {
  gap: 20px;
}

.form__container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form__bottom-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.primary-button, .primary-button-active {
  display: flex;
  align-items: center;
  padding: 20px;
  justify-content: center;
  font-size: clamp(13px, 1.2vw, 18px);
  border: 1px solid #D7D7D7;
  background-color: #FBFBF6;
  user-select: none;
  transition: border 0.9s, color 0.9s;
  color: rgb(0, 0, 0);
}
.primary-button:hover, .primary-button-active:hover {
  border: 1px solid #1B1A19;
  transition: border 0.9s, color 0.9s;
}
.primary-button:disabled, .primary-button-active:disabled {
  border: 1px solid #D7D7D7;
  background-color: #FBFBF6;
  cursor: auto;
  transition: border 0.9s, color 0.9s;
  color: rgb(147, 147, 147);
}
@media (max-width: 620px) {
  .primary-button, .primary-button-active {
    padding: 10px;
  }
}

.primary-button-active {
  border: 1px solid #1B1A19;
}
.primary-button-active:hover {
  border: 1px solid #D7D7D7;
}

.back-button {
  display: flex;
  align-content: center;
  align-items: center;
  gap: 8px;
  font-size: 16px;
  transition: color 0.9s;
}
.back-button p, .back-button img {
  color: #2A2835;
  stroke: #2A2835;
  transition: color 0.9s;
}
.back-button p:hover, .back-button img:hover {
  color: #8C8C8C;
  stroke: #8C8C8C;
  transition: color 0.9s;
}

.search-button, .search-button-address {
  display: flex;
  align-content: center;
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 30px;
  transition: color 0.9s;
}
.search-button-address {
  padding: 20px;
}

.close-button {
  display: flex;
  padding: 10px;
  border: 1px solid #D7D7D7;
  align-items: center;
  transition: border 0.9s, color 0.9s;
}
.close-button:hover {
  border: 1px solid #1B1A19;
  transition: border 0.9s, color 0.9s;
}

.scroll-button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 50px;
  right: 50px;
  z-index: 1;
  height: 50px;
  width: 50px;
  font-size: 20px;
  border: none;
  fill: #FBFBF6;
  border-radius: 100%;
  cursor: pointer;
  visibility: hidden;
  pointer-events: none;
}
@media (max-width: 620px) {
  .scroll-button {
    bottom: 25px;
    right: 25px;
  }
}

.scroll-button:not(:hover) {
  background: #2A2835;
}

.scroll-button:hover {
  background: #1B1A19;
}

.scroll-button.active {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.backToTopBtn:not(.active) {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal-right, .sizeGuide-right, .address-right {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  right: 0;
  pointer-events: none;
  transition: background-color 0.9s ease-in-out;
  z-index: 9999;
}
.modal-right.active, .active.sizeGuide-right, .active.address-right {
  background-color: rgba(0, 0, 0, 0.4);
  pointer-events: all;
  z-index: 9999;
  transition: background-color 0.9s ease-in-out;
}
.modal-right.active .modal-right-content, .active.sizeGuide-right .modal-right-content, .active.address-right .modal-right-content {
  right: 0;
  transition: 0.9s right ease-in-out;
}
.modal-right .modal-right-content, .sizeGuide-right .modal-right-content, .address-right .modal-right-content {
  display: flex;
  flex-direction: column;
  gap: 40px;
  height: 100vh;
  width: 542px;
  right: -50vw;
  padding: 40px 60px;
  background-color: #FBFBF6;
  top: 0;
  position: fixed;
  overflow: auto;
  transition: 0.9s right ease-in-out;
  z-index: 9999;
}

.modal-right-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

body .buttonchik {
  position: relative;
  height: 30px;
}
body .buttonchik button {
  position: absolute;
  right: 30px;
  top: 20px;
}
body .modal-top {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 100px;
  right: 0;
  pointer-events: none;
  transition: background-color 0.9s ease-in-out;
}
@media (max-width: 480px) {
